export const useShare = () => {

    const share = async (title: string, url: string) => {
        if (navigator.share) {
            navigator.share({
                title,
                url,
            })
                .then(() => console.log(' .'))
                .catch((error) => console.log('   .', error));
        } else {
            alert('url !');
        }
    }

    return {
        share
    }

}
